@media (min-width: 320px) and (max-width: 480px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    height: 100%;
  }
  .hero__conteiner--left {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    background-image: image-set(url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg"));
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .hero__conteiner--right {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding-top: 0%;
    height: 100vh;
    width: 100vw;
    text-align: center;
  }
  .hero__conteiner--title {
    width: 100vw;
  }
  .hero__conteiner--title h1 {
    font-size: 4rem;
  }
  .hero__conteiner--paragraph {
    height: auto;
    width: 100vw;
  }
  .hero__conteiner--paragraph p {
    font-size: 2rem;
    line-height: 2.3rem;
  }
  .hero__conteiner--btn {
    font-size: 2rem;
    background-color: rgb(73, 59, 59);
  }
}